
import hoverEffect from 'hover-effect';
import img1 from '../images/osaka/1.jpg';
import img2 from '../images/osaka/2.jpg';
import displacementImg from '../images/4.png';
//import hoverEffect from '../hover.js';

new hoverEffect({
    parent: document.querySelector('.item'),
    intensity1: 0.2,
    intensity2: 0.1,
    angle2: Math.PI / 2,
    image1: img1,
    image2: img2,
    displacementImage: displacementImg
});



